<template>
  <div class="quick-order">
    <!--  -->
    <div class="list">
      <!-- contentfor -->
      <div class="content" v-for="item in preducts" :key="item.id">
        <img :src="item.imgURL" alt="" class="img" />
        <div class="descript">
          <i class="el-icon-close delete" @click="deleteItem"></i>
          <p class="name">{{ item.name }}</p>
          <p class="format">{{ item.format[0].name }}</p>
          <el-input-number
            v-model="item.num"
            :min="1"
            :max="10"
            label=""
          ></el-input-number>
          <div class="money">
            <p>
              <span>{{ item.num }}</span
              ><span> X</span> ${{
                item.discount ? item.price * item.discount : item.price
              }}
            </p>
            <p class="sum">
              ${{
                item.num *
                (item.discount ? item.price * item.discount : item.price)
              }}
            </p>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div>
      <i class="el-icon-delete-solid clear" @click="clearCart"
        ><span></span></i
      >
      <div class="total">
        <p class="total-number"></p>
        <p class="total-number">NT${{ totalNumber }}</p>
      </div>
    </div>

    <button type="button" class="btn-order action">
      <router-link to="/order" class="btn-text"></router-link>
    </button>
  </div>
</template>

<script>
const jsData = {
  products: [
    {
      id: 37,
      parentId: 21,
      num: 2,
      name: "",
      price: 250,
      discount: 0.9,
      imgURL: require("../assets/images/product/makeup/nail-polish-01.jpg"),
      shot_des: "",
      format: [
        {
          id: 1,
          name: "Q1",
        },
        {
          id: 2,
          name: "Q2",
        },
        {
          id: 3,
          name: "Q3",
        },
        {
          id: 4,
          name: "Q4",
        },
      ],
      directions: "1.2.",
      use: "1.",
      details: [
        {
          id: 1,
          title: "",
          content: "",
        },
        {
          id: 2,
          title: "",
          content: "7ml",
        },
        {
          id: 3,
          title: "",
          content: "",
        },
        {
          id: 4,
          title: "",
          content:
            "1.2.3.",
        },
      ],
    },
    {
      id: 41,
      parentId: 39,
      num: 1,
      name: "",
      price: 490,
      discount: 0.9,
      imgURL: require("../assets/images/product/fragrance/fragrance0.1.jpg"),
      shot_des: "",
      format: [
        {
          id: 1,
          name: "",
        },
        {
          id: 2,
          name: "",
        },
        {
          id: 3,
          name: "",
        },
        {
          id: 4,
          name: "",
        },
        {
          id: 5,
          name: "",
        },
        {
          id: 6,
          name: "",
        },
      ],
      directions: "1.",
      use: "1.",
      details: [
        {
          id: 1,
          title: "",
          content: "",
        },
        {
          id: 2,
          title: "",
          content: "150ml",
        },
        {
          id: 3,
          title: "",
          content: "",
        },
        {
          id: 4,
          title: "",
          content:
            "1.2.3.",
        },
      ],
    },
  ],
};

export default {
  name: "QuickOrder",
  data() {
    return {
      preducts: jsData.products,
      totalNumber: 0,
    };
  },
  // numdeepnum
  watch: {
    preducts: {
      handler: function (newValue) {
        this.preducts = newValue;
        this.calculateTotal();
      },
      deep: true,
      immediate: true,
    },
  },
  created() {
    this.calculateTotal();
  },
  methods: {
    deleteItem() {
      console.log(
        "filterid"
      );
    },
    clearCart() {
      console.log(" []");
    },
    calculateTotal() {
      this.totalNumber = 0;
      const sumArr = [];
      this.preducts.forEach((item) => {
        const sum =
          item.num * (item.discount ? item.price * item.discount : item.price);
        sumArr.push(sum);
      });

      for (let i = 0; i < sumArr.length; i++) {
        this.totalNumber = this.totalNumber + sumArr[i];
      }
      console.log(this.totalNumber);
    },
  },
};
</script>

<style scoped>
.quick-order {
  width: 90%;
  margin: 0 auto;
}
/*  */
.content {
  display: flex;
  flex-direction: row;
  margin: 2rem 0;
  height: 150px;
  padding: 1rem 0;
  border-bottom: 1px solid var(--black);
}
.descript {
  flex: 1;
  min-width: 250px;
  padding-left: 1rem;
}
.delete {
  float: right;
  padding-right: 1rem;
  cursor: pointer;
}
.name {
  font-size: var(--sec-font-size);
  font-weight: 700;
}
.name,
.format {
  margin-bottom: 1rem;
}
.money {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 1rem;
  padding-right: 1rem;
}
.total {
  text-align: right;
  float: right;
  padding-right: 1rem;
  line-height: 2rem;
  font-size: var(--sec-font-size);
}
/*  */
.clear {
  cursor: pointer;
}
.btn-order {
  width: 100%;
  margin-top: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}
.btn-text {
  color: var(--white);
}

@media screen and (max-width: 768px) {
  .quick-order {
    width: 80%;
    margin: 0 auto;
  }
  .content {
    all: unset;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 2rem 0;
    border-bottom: 1px solid var(--black);
  }
  .descript {
    all: unset;
    margin: 1rem 0;
  }
}
</style>
