        this.bannerHeight = this.$refs.imgHeight[0].height;
      });
    },
  },
  mounted() {
    window.addEventListener(
      "resize",
      () => {
        this.bannerHeight = this.$refs.imgHeight[0].height;
      },
      false
    );
  },
};
</script>

